import React, { Component } from 'react';
import {Routes,Link, Route} from 'react-router-dom';

import './App.css';
import Details from './Details';

function App(props) {
  const data = props.sampleData;
  return (
    <div className="App">
        <h3>Listing Screen</h3>
      <div className='table-container'>
        <table className="table table-striped">
          <thead className='thead-dark'>
            <tr>
              <th scope="col">Id</th>
              <th scope="col">Name</th>
              <th scope="col">Status</th>
            </tr>
          </thead>
          <tbody>
            {data && data.map((data, index) => {
              return (
                <tr key = {index}>
                  <th scope="row" className='text-left'><Link to='/details/{data.id}'>{data.id}</Link></th>
                  <td className='text-left'>{data.name}</td>
                  <td className='text-left'>{data.status}</td>
                </tr>
              )
            })}
          </tbody>
        </table>
      </div>
      <Routes>
        <Route path = '/details/id:id' element={<Details/>} ></Route>
      </Routes>
    </div>
  );
}

export default App;
